<template>
    <div class="f-treetable panel">
        <div class="panel-heading">
            server
        </div>
        <div class="panel-body">
            <div class="yz-treetable">
                <table class="table table-bordered">
                    <colgroup>
                        <col width="100px">
                        <col width="300px">
                        <col>
                    </colgroup>
                    <thead>
                        <tr>
                            <th>序号</th>
                            <th>姓名</th>
                            <th>年龄</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(item, $index) in dealData" >
                            <td style="text-align:left;">
                                <div class="table-tree-icon" :style="iconStyle(item)" @click="toggleChildren(item,$index)">
                                    <i class="yz-icon" :class="iconClass(item)"></i>
                                </div>
                            </td>
                            <td>{{item.name}}</td>
                            <td>{{item.age}}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: "treeTable",
    componentName: "treeTable",
    props: {
        data: Array
    },
    data: function() {
        return {
            dealData: this.data.concat()
        }
    },
    computed: {

    },
    created: function() {
        this.setParent(this.data, 1, -1);
        console.log(this.dealData)
    },
    methods: {
        setParent: function(e, t, n) {
            console.log(e, t, n);
            for (var a = e.length, r = 0; r < a; r++) {
                var o = e[r];
                if (o.currentLevel = t, o.parentIndex = -1 == n ? null : n, console.log(o.children), o.children && 0 != o.children.length) {
                    var l = -1 === n ? r : o.parentIndex + "-" + r;
                    this.setParent(o.children, t + 1, l)
                }
            }
        },
        iconStyle: function(e) {
            return {
                marginLeft: 10 * e.currentLevel + "px"
            }
        },
        iconClass: function(e) {
            return e.children && 0 != e.children.length ? e.isOpen ? {
                "icon-open": !0
            } : {
                "icon-close": !0
            } : {
                "icon-default": !0
            }
        },
        toggleChildren: function(e, t) {
            console.log("toggle", e, t), e.children && 0 != e.children.length && (e.isOpen ? this.closeRow(e, t) : this.openRow(e, t))
        },
        openRow: function(e, t) {
            if (!e.isOpen) {
                for (var n = 0; n < e.children.length; n++) this.dealData.splice(t + n + 1, 0, e.children[n]);
                e.isOpen = !0
            }
        },
        closeRow: function(e, t) {
            if (e.isOpen) {
                for (var n = e.currentLevel, a = 0, r = t + 1, o = this.dealData.length; r < o; r++) {
                    var l = this.dealData[r];
                    if (l.isOpen = !1, console.log(l.currentLevel >= n), l.currentLevel <= n) {
                        a = r - t - 1;
                        break
                    }
                }
                console.log(a), this.dealData.splice(t + 1, a), e.isOpen = !1
            }
        }
    }
}
</script>
<style>

.pc-header {
    height: 60px
}
.f-treetable .td{
    text-align: left;
}
.f-treetable .table-tree-icon{
    display: inline-block;
    margin-left: 10px;
}
.f-treetable .yz-icon {
    position: relative;
    display: inline-block;
    height: 18px;
    width: 18px;

}

.f-treetable .yz-icon.icon-close {
    background: url("/static/img/server/folderClose.gif") 2px -1px no-repeat
}

.f-treetable .yz-icon.icon-open {
    background: url("/static/img/server/folderOpen.gif") 2px -1px no-repeat
}

.f-treetable .yz-icon.icon-default {
    background: url("/static/img/server/defaultLeaf.gif") 2px -1px no-repeat
}
</style>



