<template>
    <div class="f-index pc-header container">
        <el-menu class="el-menu-demo" theme="dark" mode="horizontal">
            <el-menu-item index="1">处理中心</el-menu-item>
            <el-submenu index="2">
                <template slot="title">我的工作台</template>
                <el-menu-item index="2-1">选项1</el-menu-item>
                <el-menu-item index="2-2">选项2</el-menu-item>
                <el-menu-item index="2-3">选项3</el-menu-item>
            </el-submenu>
        </el-menu>
        <div class="pc-body">
            <serverTable :data="data"></serverTable>
        </div>
    </div>
</template>
<script>
import serverTable from '../components/treeTable.vue'

export default {
    name: "serverBody",
    components:{
        serverTable
    },
    data: function() {
        return {
            data: [{
                name: "gzh1",
                age: 11,
                children: [{
                    name: "gzh1-1",
                    age: 111
                }, {
                    name: "gzh1-2",
                    age: 112
                }]
            }, {
                name: "gzh2",
                age: 11
            }, {
                name: "gzh3",
                age: 11,
                children: [{
                    name: "gzh3-1",
                    age: 111
                }, {
                    name: "gzh3-2",
                    age: 112,
                    children: [{
                        name: "gzh3-2-1",
                        age: 111,
                        children: [{
                            name: "gzh3-2-1-1",
                            age: 111
                        }]
                    }]
                }]
            }, {
                name: "gzh4",
                age: 11
            }]
        }
    },
    created: function() {
        this.getData()
    },
    methods: {
        getData: function() {
            var e = {};
            $.ajax({
                url: "http://localhost:10000/monitor/queryCrossServices.do",
                type: "post",
                headers: {
                    Accept: "application/json",
                    "Content-Type": "application/json"
                },
                data: {},
                dataType: "json",
                success: function(e) {
                    console.log(e)
                },
                error: function() {
                    console.log("更新用户服务请求失败！")
                }
            });
        }
    }

}
</script>



